
* {
  --var-color : rgb(100, 168, 53)
}


.App {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--var-color);
}

.quote {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
  max-width: 70%;
  min-width: 500px;
  padding: 3em;
  border-radius: 30px 0 30px 0;
  background-color: white;
  margin-bottom: 30px;
}

h1, h3 {
  color : var(--var-color);
}

h3 {
  text-align: end;
}

.btns {
  display: flex;
}

.extra-btns {
  top: 0;
  position: absolute;
  margin: 10px 0 0 0;
}

.extra-btn {
  background-color: white;
  color: var(--var-color);
}

button {
  padding: 1em;
  background-color: var(--var-color);
  color: white;
  border-radius: 5px 0;
  border: none;
  margin: 0 1em;
  cursor: pointer;
}

a{
  text-decoration: none;
  color: white;
}
table {
  font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
  width: 80%;
  margin: 0 auto;
}

td, th {
  border:1px solid #ddd;
  padding: 8px;
}

tr:hover {background-color: #ddd;}

tr:nth-child(even){background-color: #f2f2f2;}

th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: rgb(100, 168, 53);
  color: white;
}